export default class LocalStorageManager {
  set(obj) {
    let currentState = JSON.stringify(obj);
    localStorage.setItem("recipeBox", currentState);
  }
  get() {
    let currentState = localStorage.getItem("recipeBox");
    return JSON.parse(currentState);
  }
}
